import { type NextRequest, NextResponse } from 'next/server';
import { PrismaClient } from '@prisma/client';

const prisma = new PrismaClient();

// GET - ุฌูุจ ูุดุฑูุน ูุงุญุฏ
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const resolvedParams = await params;
    const param = resolvedParams.id;

    // ุงูุณูุงุญ ุจุงุณุชุฎุฏุงู ุงููุนุฑู ุฃู ุงูslug
    let project = await prisma.projects.findUnique({
      where: { id: param },
      include: {
        media_items: { orderBy: { order: 'asc' } },
        project_tags: true,
        project_materials: true,
        _count: { select: { comments: true, project_likes: true } }
      }
    });

    if (!project) {
      project = await prisma.projects.findUnique({
        where: { slug: param },
        include: {
          media_items: { orderBy: { order: 'asc' } },
          project_tags: true,
          project_materials: true,
          _count: { select: { comments: true, project_likes: true } }
        }
      });
    }

    if (!project) {
      return NextResponse.json({ error: 'ุงููุดุฑูุน ุบูุฑ ููุฌูุฏ' }, { status: 404 });
    }

    // ุฒูุงุฏุฉ ุนุฏุฏ ุงููุดุงูุฏุงุช ุจุงุณุชุฎุฏุงู ูุนุฑู ุงููุดุฑูุน ุงูุญูููู
    await prisma.projects.update({
      where: { id: project.id },
      data: { views: { increment: 1 } }
    });

    console.log('๐ ุชู ุฌูุจ ุงููุดุฑูุน:', project.title);

    return NextResponse.json({
      ...project,
      mediaItems: (project as any).media_items,
      tags: (project as any).project_tags || [],
      materials: (project as any).project_materials || [],
      views: (project.views || 0) + 1,
      likes: (project._count as any)?.project_likes || 0,
      rating: project.rating || 0
    });

  } catch (error) {
    console.error('โ ุฎุทุฃ ูู ุฌูุจ ุงููุดุฑูุน:', error);
    return NextResponse.json(
      { error: 'ุญุฏุซ ุฎุทุฃ ูู ุฌูุจ ุงููุดุฑูุน' },
      { status: 500 }
    );
  }
}

// PUT - ุชุนุฏูู ูุดุฑูุน
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const resolvedParams = await params;
    const projectId = resolvedParams.id;
    const data = await request.json();
    console.log('๐ง ุชุนุฏูู ุงููุดุฑูุน:', projectId, data);

    const {
      title,
      description,
      category,
      location,
      completionDate,
      client,
      featured,
      projectDuration,
      projectCost,
      mediaItems,
      tags,
      materials
    } = data;

    // ุงูุชุญูู ูู ุตุญุฉ ุงูุจูุงูุงุช
    if (!title || !description || !category || !location) {
      return NextResponse.json(
        { error: 'ุงูุจูุงูุงุช ุงูุฃุณุงุณูุฉ ูุทููุจุฉ' },
        { status: 400 }
      );
    }

    // ุงูุชุญูู ูู ูุฌูุฏ ุงููุดุฑูุน
    const existingProject = await prisma.projects.findUnique({
      where: { id: projectId },
      include: {
        mediaItems: true,
        tags: true,
        materials: true
      }
    });

    if (!existingProject) {
      return NextResponse.json(
        { error: 'ุงููุดุฑูุน ุบูุฑ ููุฌูุฏ' },
        { status: 404 }
      );
    }

    // ุญุฐู ุงููุณุงุฆุท ูุงูุนูุงูุงุช ูุงูููุงุฏ ุงููุฏููุฉ
    await prisma.media_items.deleteMany({
      where: { projectId }
    });

    await prisma.project_tags.deleteMany({
      where: { projectId }
    });

    await prisma.project_materials.deleteMany({
      where: { projectId }
    });

    // ุชุญุฏูุซ ุงููุดุฑูุน ูุน ุงูุจูุงูุงุช ุงูุฌุฏูุฏุฉ
    const updatedProject = await prisma.projects.update({
      where: { id: projectId },
      data: {
        title,
        description,
        category,
        location,
        completionDate: new Date(completionDate),
        client: client || null,
        featured: featured || false,
        projectDuration: projectDuration || '',
        projectCost: projectCost || '',
        updatedAt: new Date(),
        media_items: {
          create: mediaItems?.map((item: { type: string; src: string; thumbnail?: string; title?: string; description?: string; duration?: number }, index: number) => ({
            type: item.type,
            src: item.src,
            thumbnail: item.thumbnail || item.src,
            title: item.title || `ููู ${index + 1}`,
            description: item.description || '',
            duration: item.duration || null,
            order: index
          })) || []
        },
        project_tags: {
          create: tags?.map((tag: string | { name: string }) => ({
            name: typeof tag === 'string' ? tag : tag.name
          })) || []
        },
        project_materials: {
          create: materials?.map((material: string | { name: string }) => ({
            name: typeof material === 'string' ? material : material.name
          })) || []
        }
      },
      include: {
        media_items: true,
        project_tags: true,
        project_materials: true,
        _count: {
          select: {
            comments: true,
            project_likes: true,
            project_views: true
          }
        }
      }
    });

    console.log('โ ุชู ุชุญุฏูุซ ุงููุดุฑูุน ุจูุฌุงุญ:', updatedProject.title);

    // ุฅุด๏ฟฝ๏ฟฝุงุฑ ุฌูุฌู ุจุงูุชุญุฏูุซ
    try {
      await fetch(`${process.env.NEXT_PUBLIC_BASE_URL || 'http://localhost:3000'}/api/sitemap/refresh`, {
        method: 'POST'
      });
    } catch (error) {
      console.warn('ุชุนุฐุฑ ุฅุดุนุงุฑ ุฌูุฌู ุจุงูุชุญุฏูุซ:', error);
    }

    return NextResponse.json({
      success: true,
      project: { ...updatedProject, mediaItems: (updatedProject as any).media_items },
      message: 'ุชู ุชุญุฏูุซ ุงููุดุฑูุน ุจูุฌุงุญ'
    });

  } catch (error: unknown) {
    console.error('โ ุฎุทุฃ ูู ุชุญุฏูุซ ุงููุดุฑูุน:', error);
    return NextResponse.json(
      { error: 'ุญุฏุซ ุฎุทุฃ ูู ุชุญุฏูุซ ุงููุดุฑูุน' },
      { status: 500 }
    );
  }
}

// DELETE - ุญุฐู ูุดุฑูุน
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const resolvedParams = await params;
    const projectId = resolvedParams.id;

    // ุงูุชุญูู ูู ูุฌูุฏ ุงููุดุฑูุน
    const existingProject = await prisma.projects.findUnique({
      where: { id: projectId },
      include: {
        media_items: true,
        project_tags: true,
        project_materials: true,
        comments: true
      }
    });

    if (!existingProject) {
      return NextResponse.json(
        { error: 'ุงููุดุฑูุน ุบูุฑ ููุฌูุฏ' },
        { status: 404 }
      );
    }

    console.log('๐๏ธ ุญุฐู ุงููุดุฑูุน:', existingProject.title);

    // ุญุฐู ุงูุจูุงูุงุช ุงููุฑุชุจุทุฉ ุฃููุงู
    await prisma.comments.deleteMany({
      where: { projectId }
    });

    await prisma.media_items.deleteMany({
      where: { projectId }
    });

    await prisma.project_tags.deleteMany({
      where: { projectId }
    });

    await prisma.project_materials.deleteMany({
      where: { projectId }
    });

    // ุญุฐู ุงููุดุฑูุน
    await prisma.projects.delete({
      where: { id: projectId }
    });

    console.log('โ ุชู ุญุฐู ุงููุดุฑูุน ุจูุฌุงุญ');

    // ุฅุดุนุงุฑ ุฌูุฌู ุจุงูุญุฐู
    try {
      await fetch(`${process.env.NEXT_PUBLIC_BASE_URL || 'http://localhost:3000'}/api/sitemap/refresh`, {
        method: 'POST'
      });
    } catch (error) {
      console.warn('ุชุนุฐุฑ ุฅุดุนุงุฑ ุฌูุฌู ุจุงูุญุฐู:', error);
    }

    return NextResponse.json({
      success: true,
      message: 'ุชู ุญุฐู ุงููุดุฑูุน ุจูุฌุงุญ'
    });

  } catch (error: unknown) {
    console.error('โ ุฎุทุฃ ูู ุญุฐู ุงููุดุฑูุน:', error);
    return NextResponse.json(
      { error: 'ุญุฏุซ ุฎุทุฃ ูู ุญุฐู ุงูู๏ฟฝ๏ฟฝุฑูุน' },
      { status: 500 }
    );
  }
}
